<template>
  <el-drawer
    class="customDrawer"
    :model-value="visible"
    :size="props.width"
    @open="open"
    @close="close"
  >
    <template #header>
      <header>{{ props.title }}</header>
    </template>
    <template #default>
      <div class="body-container">
        <slot name="default"></slot>
      </div>
    </template>
    <template #footer>
      <slot name="footer"></slot>
    </template>
  </el-drawer>
</template>

<script lang="ts" setup>
import { ref } from "vue";

const props = withDefaults(
  defineProps<{
    title: string;
    width?: string | number;
    open?: () => void;
    close?: () => void;
  }>(),
  {
    width: 500,
  }
);

const visible = ref(false);
const open = () => {
  props.open && props.open();
};
const close = () => {
  visible.value = false;
  props.close && props.close();
};
const toggle = (value: boolean) => {
  visible.value = value;
  return Promise.resolve({
    visible: visible.value,
  });
};
defineExpose({
  toggle,
});
</script>
<style lang="scss">
.el-drawer.customDrawer {
  .el-drawer__header {
    padding: 20px;
    margin: 0;
    border-bottom: 1px solid #edf1f6;
  }

  .el-drawer__title {
    color: #303133;
    font-size: 18px;
    line-height: 1;
  }
}
</style>
